/* Base theme variables are already defined in App.css */

/* Theme Modal Styles */
.theme-selector-modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2000;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s, visibility 0.3s;
    backdrop-filter: blur(4px);
  }
  
  .theme-selector-modal.open {
    opacity: 1;
    visibility: visible;
  }
  
  .theme-selector-content {
    background-color: var(--card-bg);
    border-radius: var(--radius-lg);
    box-shadow: var(--shadow-lg);
    width: 90%;
    max-width: 600px;
    max-height: 90vh;
    overflow-y: auto;
    padding: 1.5rem;
    border: 2px solid var(--border); /* Thicker border */
  }
  
  .theme-selector-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;
    padding-bottom: 0.75rem;
    border-bottom: 1px solid var(--border);
  }
  
  .theme-selector-header h3 {
    font-size: 1.25rem;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0;
  }
  
  .close-button {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--text-secondary);
  }
  
  .predefined-themes,
  .custom-theme {
    margin-bottom: 2rem;
  }
  
  .predefined-themes h4,
  .custom-theme h4 {
    font-size: 1rem;
    font-weight: 600;
    margin-bottom: 1rem;
    color: var(--text-primary);
  }
  
  .theme-buttons {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    gap: 1rem;
  }
  
  .theme-button {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: none;
    border: 2px solid var(--border); /* Thicker border */
    border-radius: var(--radius-md);
    padding: 0.75rem;
    cursor: pointer;
    transition: var(--transition);
  }
  
  .theme-button:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-md);
  }
  
  .theme-button.active {
    border-color: var(--primary);
    background-color: rgba(99, 102, 241, 0.1);
    box-shadow: 0 0 0 1px var(--primary); /* Glowing effect for active theme */
  }
  
  .theme-preview {
    width: 60px;
    height: 60px;
    border-radius: var(--radius-sm);
    margin-bottom: 0.5rem;
    border: 2px solid var(--border); /* Thicker border */
    overflow: hidden;
  }
  
  .theme-button span {
    font-size: 0.875rem;
    color: var(--text-primary);
  }
  
  /* Theme Preview Colors */
  .theme-preview.light-theme {
    background: linear-gradient(135deg, #f9fafb 50%, #ffffff 50%);
    border: 2px solid #d1d5db;
  }

  .theme-preview.dark-theme {
    background: linear-gradient(135deg, #1e293b 50%, #334155 50%);
    border: 2px solid #475569;
  }

  .theme-preview.sunset-theme {
    background: linear-gradient(135deg, #fffbeb 50%, #f59e0b 50%);
    border: 2px solid #fbbf24;
  }

  .theme-preview.ocean-theme {
    background: linear-gradient(135deg, #f0f9ff 50%, #0ea5e9 50%);
    border: 2px solid #7dd3fc;
  }

  .theme-preview.forest-theme {
    background: linear-gradient(135deg, #f0fdf4 50%, #16a34a 50%);
    border: 2px solid #86efac;
  }

  /* New Theme Previews */
  .theme-preview.purple-haze-theme {
    background: linear-gradient(135deg, #f5f3ff 50%, #8b5cf6 50%);
    border: 2px solid #a78bfa;
  }

  .theme-preview.coral-reef-theme {
    background: linear-gradient(135deg, #fff1f2 50%, #f43f5e 50%);
    border: 2px solid #fb7185;
  }

  .theme-preview.midnight-blue-theme {
    background: linear-gradient(135deg, #0f172a 50%, #3b82f6 50%);
    border: 2px solid #3b82f6;
  }

  .theme-preview.amber-glow-theme {
    background: linear-gradient(135deg, #ffedd5 50%, #f97316 50%);
    border: 2px solid #fb923c;
  }

  .theme-preview.teal-breeze-theme {
    background: linear-gradient(135deg, #f0fdfa 50%, #14b8a6 50%);
    border: 2px solid #2dd4bf;
}
  
  /* Custom Theme Section */
  .color-pickers {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
    gap: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .color-picker input[type="color"] {
    width: 100%;
    height: 40px;
    border: 2px solid var(--border); /* Thicker border */
    border-radius: var(--radius-sm);
    padding: 2px;
    cursor: pointer;
    outline: none;
  }
  
  .color-picker label {
    font-size: 0.875rem;
    color: var(--text-secondary);
  }
  
  .color-picker input[type="color"] {
    width: 100%;
    height: 40px;
    border: 1px solid var(--border);
    border-radius: var(--radius-sm);
    padding: 2px;
    cursor: pointer;
  }
  
  .preview-custom-theme {
    margin-bottom: 1.5rem;
    border-radius: var(--radius-md);
    overflow: hidden;
  }
  
  .preview-panel {
    padding: 1rem;
    border-radius: var(--radius-md);
    border: 2px solid var(--border); /* Thicker border */
  }
  
  .preview-header {
    font-weight: 600;
    margin-bottom: 0.75rem;
    padding-bottom: 0.5rem;
    border-bottom: 2px solid var(--border); /* Thicker border */
  }
  
  .preview-content {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .preview-content button {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: var(--radius-sm);
    font-weight: 500;
    cursor: pointer;
    align-self: flex-start;
  }
  
  .apply-custom-theme {
    padding: 0.625rem 1.25rem;
    background-color: var(--primary);
    color: white;
    border: none;
    border-radius: var(--radius-md);
    cursor: pointer;
    font-weight: 500;
    font-size: 0.875rem;
    transition: var(--transition);
    width: 100%;
  }
  
  .apply-custom-theme:hover {
    background-color: var(--primary-hover);
  }
  
  /* Theme Change Button in Sidebar */
  .theme-change-button {
    margin-top: auto;
    padding: 0.75rem 1rem;
    position: static;
    width: 100%;
    background-color: rgba(99, 102, 241, 0.1);
    color: var(--primary);
    border: 1px solid var(--border);
    border-radius: var(--radius-md);
    cursor: pointer;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    transition: var(--transition);
  }
  
  .theme-change-button:hover {
    background-color: rgba(99, 102, 241, 0.2);
  }
  
  .theme-change-button svg {
    width: 1.25rem;
    height: 1.25rem;
  }
  
  /* Light Theme (Default) */
  .light-theme {
    --primary: #6366f1;
    --primary-hover: #4f46e5;
    --background: #f9fafb;
    --card-bg: #ffffff;
    --text-primary: #1f2937;
    --text-secondary: #6b7280;
    --border: #d1d5db;  /* Darker border for better distinction */
    --error: #ef4444;
    --success: #10b981;
  }

  /* Dark Theme - Improved */
  .dark-theme {
    --primary: #818cf8;
    --primary-hover: #6366f1;
    --background: #1e293b;  /* Lighter background for better contrast */
    --card-bg: #334155;     /* Lighter card background */
    --text-primary: #f8fafc;
    --text-secondary: #e2e8f0;
    --border: #475569;      /* More visible border */
    --error: #f87171;
    --success: #34d399;
  }

  /* Sunset Theme */
  .sunset-theme {
    --primary: #f59e0b;
    --primary-hover: #d97706;
    --background: #fffbeb;
    --card-bg: #ffffff;
    --text-primary: #78350f;
    --text-secondary: #b45309;
    --border: #fbbf24;      /* More pronounced border */
    --error: #ef4444;
    --success: #10b981;
  }

  /* Ocean Theme */
  .ocean-theme {
    --primary: #0ea5e9;
    --primary-hover: #0284c7;
    --background: #f0f9ff;
    --card-bg: #ffffff;
    --text-primary: #0c4a6e;
    --text-secondary: #0369a1;
    --border: #7dd3fc;      /* More visible border */
    --error: #ef4444;
    --success: #10b981;
  }

  /* Forest Theme */
  .forest-theme {
    --primary: #16a34a;
    --primary-hover: #15803d;
    --background: #f0fdf4;
    --card-bg: #ffffff;
    --text-primary: #14532d;
    --text-secondary: #15803d;
    --border: #86efac;      /* More visible border */
    --error: #ef4444;
    --success: #10b981;
  }

  /* New Themes */
  /* Purple Haze Theme */
  .purple-haze-theme {
    --primary: #8b5cf6;
    --primary-hover: #7c3aed;
    --background: #f5f3ff;
    --card-bg: #ffffff;
    --text-primary: #4c1d95;
    --text-secondary: #6d28d9;
    --border: #a78bfa;      /* Stronger border color */
    --error: #ef4444;
    --success: #10b981;
  }

  /* Coral Reef Theme */
  .coral-reef-theme {
    --primary: #f43f5e;
    --primary-hover: #e11d48;
    --background: #fff1f2;
    --card-bg: #ffffff;
    --text-primary: #881337;
    --text-secondary: #be123c;
    --border: #fb7185;      /* More vibrant border */
    --error: #ef4444;
    --success: #10b981;
  }

  /* Midnight Blue Theme - Improved */
  .midnight-blue-theme {
    --primary: #3b82f6;
    --primary-hover: #2563eb;
    --background: #0f172a;  /* Slightly lighter for better usability */
    --card-bg: #1e293b;     /* More distinguishable card background */
    --text-primary: #f8fafc;
    --text-secondary: #cbd5e1;
    --border: #3b82f6;      /* Blue border for better distinction */
    --error: #f87171;
    --success: #34d399;
  }

  /* Amber Glow Theme */
  .amber-glow-theme {
    --primary: #f97316;
    --primary-hover: #ea580c;
    --background: #ffedd5;
    --card-bg: #ffffff;
    --text-primary: #7c2d12;
    --text-secondary: #c2410c;
    --border: #fb923c;      /* More pronounced border */
    --error: #ef4444;
    --success: #10b981;
  }

  /* Teal Breeze Theme */
  .teal-breeze-theme {
    --primary: #14b8a6;
    --primary-hover: #0d9488;
    --background: #f0fdfa;
    --card-bg: #ffffff;
    --text-primary: #134e4a;
    --text-secondary: #0f766e;
    --border: #2dd4bf;      /* More visible border */
    --error: #ef4444;
    --success: #059669;
  }
  
  /* Media Queries */
  @media (max-width: 768px) {
    .theme-buttons {
      grid-template-columns: repeat(2, 1fr);
    }
    
    .color-pickers {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (max-width: 480px) {
    .theme-buttons {
      grid-template-columns: repeat(1, 1fr);
    }
    
    .color-pickers {
      grid-template-columns: 1fr;
    }
  }

  /* Styles for editable title */
.title-edit-input {
    font-size: 1.5rem;
    font-weight: bold;
    background: transparent;
    border: 1px solid var(--border, #ccc);
    border-radius: 4px;
    padding: 4px 8px;
    color: var(--text-primary);
    width: 100%;
  }
  
  /* Styles for archived cards */
  .card-archived {
    background-color: rgba(240, 240, 240, 0.7);
    border: 1px solid #e0e0e0;
  }
  
  .disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  /* Make title clickable */
  .title-container h1 {
    cursor: pointer;
  }
  
  /* Add hover effect to title pencil icon */
  .title-container svg:hover {
    transform: scale(1.1);
  }


  /* Add these styles to your App.css or create a new CSS file */

.error-input {
    border: 1px solid #ff3860 !important;
    background-color: rgba(255, 56, 96, 0.05);
  }
  
  .error-message {
    color: #ff3860;
    font-size: 0.8rem;
    margin-top: 0.25rem;
  }
  
  /* Style for the visit button when URL is invalid */
  .visit-button:disabled {
    background-color: #e9ecef;
    cursor: not-allowed;
  }
  
  /* Adding a tooltip to show when URL is invalid */
  [title] {
    position: relative;
  }
  
  /* Ensure icon previews handle errors gracefully */
  .icon-preview img {
    max-width: 100px;
    max-height: 100px;
    display: block;
    margin: 10px 0;
    border-radius: 4px;
  }

  .card {
    background: var(--card-bg);
    border-radius: var(--radius-md);
    box-shadow: var(--shadow-sm);
    transition: all var(--transition);
    overflow: hidden;
    border: 1px solid var(--border); /* Add visible border to all cards */
  }
  
  .card.card-list {
    border-left: 3px solid var(--primary); /* Left accent border for list view */
  }
  
  /* Dark theme specific improvements */
  .dark-theme .card {
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3); /* Stronger shadow for dark theme */
  }
  
  .dark-theme .theme-button {
    background-color: rgba(255, 255, 255, 0.05); /* Slightly visible background */
  }
  
  .dark-theme .form-group input,
  .dark-theme .form-group textarea {
    background-color: var(--card-bg);
    color: var(--text-primary);
    border-color: var(--border);
  }
  
  .dark-theme .search-input {
    background-color: var(--card-bg);
    color: var(--text-primary);
  }
  
  /* Improved separation for sections */
  .theme-selector-header,
  .predefined-themes,
  .custom-theme {
    border-bottom: 2px solid var(--border); /* Thicker divider */
    padding-bottom: 1.5rem;
    margin-bottom: 1.5rem;
  }
  
  .custom-theme {
    border-bottom: none;
  }
  
  /* Better button contrast for dark themes */
  .dark-theme .apply-custom-theme,
  .midnight-blue-theme .apply-custom-theme {
    background-color: var(--primary);
    color: white;
    border: none;
    font-weight: 600;
  }
  
  /* Ensure text is readable in dark themes */
  .dark-theme .theme-button span,
  .midnight-blue-theme .theme-button span {
    color: #f1f5f9; /* Ensure light text on dark backgrounds */
  }